import { Routes, RouterModule } from '@angular/router';

import { AppComponent } from './app.component';
import {RegComponent} from './reg/reg.component';
import { WelcomeComponent } from './welcome/welcome.component';
import { SelectmeterComponent } from './selectmeter/selectmeter.component';
import { NotificationComponent} from './notification/notification.component';
import { ShowdataComponent} from './showdata/showdata.component';
const appRoutes: Routes = [
    { path: '', component: WelcomeComponent },
    
    { path: 'createprofile', component: RegComponent },
    

    { path: 'selectmeter', component: SelectmeterComponent },

    { path: 'notification', component: NotificationComponent},
    { path: 'confirmWindow', component: ShowdataComponent}


];

export const routing = RouterModule.forRoot(appRoutes);


