import { Component, OnInit } from '@angular/core';

import { ShowdataComponent} from '../showdata/showdata.component';
@Component({
  selector: 'app-selectmeter',
  templateUrl: './selectmeter.component.html',
  styleUrls: ['./selectmeter.component.css']
})
export class SelectmeterComponent implements OnInit {
  
  constructor() {  }
  usage =0;
  Bill =0;

  ngOnInit() {
    document.getElementById("confirm").style.visibility = "hidden";
  }
  name='' ;
  value = 'null';
  onEnter(value: string) { this.value = value; 
  //Send BackEnd Call to get the usage data from system 
  //Consider Usage is equal to 1000 
  this.usage =1000;
  this.Bill=1000*4;
  }
  onClick(){
    document.getElementById("confirm").style.visibility = "visible";

  }

}



